const styles = {
   slider: {
      height: '100%',
      position: 'relative',
      width: '100%'
   },
   slide: {
      height: '100%',
      left: 0,
      opacity: 0,
      position: 'absolute',
      top: 0,
      transition: 'all 0.3s',
      width: '100%',
      zIndex: 3
   },
   sliderImg: {
      height: '100%',
      objectFit: 'cover',
      opacity: '1 !important',
      display: 'block',
      width: '100%'
   },
   onTop: {
      opacity: 1,
      zIndex: 1
   },
   pagination: {
      bottom: 0,
      display: 'flex',
      justifyContent: 'center',
      left: 0,
      margin: '0 auto',
      position: 'absolute',
      right: 0,
      zIndex: 99
   },
   tile: {
      background: '#FFF',
      border: '1px solid #999',
      borderRadius: '50%',
      cursor: 'pointer',
      height: '20px',
      margin: '6px',
      transition: 'all 0.3s',
      width: '20px'
   },
   activeTile: {
      background: '#333'
   }
}

class Slider extends React.Component {
   constructor() {
      super();
      this.state = { images: [], currentSlide: 0, pagination: false };
      this.slider = React.createRef();
   }

   componentDidMount() {
      const { children, pagination } = this.props;
      const images = React.Children.map(children, child =>
         React.cloneElement(child, { style: styles.sliderImg })
      );

      this.setState({ images, pagination });

      images && setInterval(() => {
         const onTop = this.state.currentSlide;
         const currentSlide = onTop == 2 ? 0 : onTop + 1;

         this.setState({ currentSlide });
      }, 4500);
   }

   createPagination = (slides, activeTile) => {

      const tiles = slides && slides.map((slide, key) => {
         const currentIndex = slides.indexOf(slide);
         const classes = currentIndex == activeTile
            ? 'tile'
            : 'tile active-tile';
         const style = slides.indexOf(slide) == activeTile
            ? { ...styles.tile, ...styles.activeTile }
            : styles.tile;

         return (
            <div
               key={key}
               className={classes}
               style={style}
               onClick={() => this.handlePagination(currentIndex)}
            >
            </div>
         );
      });

      return (<nav className="pagination" style={styles.pagination}>{tiles}</nav>);
   }

   handlePagination = index => {
      console.log(index);
   }

   render() {
      const { images, pagination } = this.state;
      const slides = images.length > 1 && images.map((slide, key) => {
         const classes = images.indexOf(slide) == this.state.currentSlide
            ? 'slide on-top'
            : 'slide';
         const style = images.indexOf(slide) == this.state.currentSlide
            ? { ...styles.slide, ...styles.onTop }
            : styles.slide;

         return (<div key={key} className={classes} style={style}>{slide}</div>);
      });

      return (
         <div className="slider" style={styles.slider} ref={this.slider}>
            {slides}
            {pagination && this.createPagination(slides, this.state.currentSlide)}
         </div>
      );
   }
}




export default Slider;

